import { Component } from '@angular/core';
import { IonicPage, NavController,LoadingController, AlertController, NavParams } from 'ionic-angular';
import { GlobalVarsProvider } from '../../providers/global-vars/global-vars';
import { AlertServiceProvider } from '../../providers/alert-service/alert-service';
import { ClientdataProvider } from '../../providers/clientdata/clientdata';


@IonicPage()
@Component({
  selector: 'page-ministatement',
  templateUrl: 'ministatement.html',
})
export class MinistatementPage {
  response:boolean;
  datetime:string;
  data: string;
  status: string;
  actualbalance: number;
  statements: any = [];
    buton: any = true;
    myValue: any;
  accounts: any;
  title: any;
  langs: any;
  error: any;
  accounts_no: any;
  unmasked_accounts: any = [];
    objectblock: any = {
                      username:this.globalVars.getUsername(),
                      account_from: '',lang:'en',type:''
  };
  constructor(public navCtrl: NavController,private clientdata: ClientdataProvider,public globalVars: GlobalVarsProvider, public navParams: NavParams,
    public loadingController: LoadingController, public alertService: AlertServiceProvider, public alertCtrl: AlertController) {
    this.langs = this.globalVars.doTranslate( this.globalVars.getLang() );
    this.objectblock.username = this.globalVars.Username;
    this.accounts = globalVars.linked_acc;
    this.objectblock.account_from = this.accounts[ 0 ].acc_number;
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad MinistatementPage');
  }
  goHome(){
    this.navCtrl.setRoot('HomePage');
  }
  ionViewDidEnter() {
    this.globalVars.reload();

  }
  submit() {
    if ( this.objectblock.account_from === null || this.objectblock.account_from === ''|| this.objectblock.account_from === undefined ) {
      this.error = 1;
      } else {
      if(this.objectblock.account_from.substring(0, 3) == "255"){
        this.objectblock.type='MWALLET';
      }else{
        this.objectblock.type='CBS';
      }
      this.alertService.showLoading( this.langs.submitting_txt );
      this.clientdata.ministatement( this.objectblock )
        .subscribe( data => {
          data = this.globalVars.testdec( data );
          console.log( data )
          if ( data.length != 0 ) {
            this.alertService.dismiss();
            if ( !data.error ) {
             this.response = true;
             this.status = data.message;
             this.statements = data.statement;
             this.datetime = new Date().toLocaleString();
           this.buton = false;
            } else if ( data.error ) {
              this.alertService.errorPop( data.message );
            } else {
              this.alertService.errorPop( this.langs.tech );
            }
          } else {
            this.alertService.myLoadingDismiss();
            this.alertService.errorPop( this.langs.tech );
          }
        }, err => {
          console.log( err );
          this.alertService.dismiss();
          this.alertService.errorPop( this.langs.tech );
        });
    } }

}



